.loading-spinner {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  display: none;
  z-index: 1000000000000;
}

.spinner {
  position: relative;
  top: 50%;
  left: 50%;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 7px solid #f3f3f3;
  border-top: 5px solid var(--primary-color);
  animation: spin 1s linear infinite;
}

